/* eslint-disable @typescript-eslint/no-redeclare */
import { useWeb3React } from "@web3-react/core";
import { ethers } from "ethers";
import { useCallback } from "react";
import { getPoolApr } from "../utils/apr";
import { Token } from "../utils/erc20Helper";
import { getTokenValueUseUSD } from "../utils/value";
import cake_token_img from "../asserts/img/token/token-cake.svg";
import { Vault } from "../models/vault";
import { getCakeStakingPool, getGuaStrategy } from "../utils/web3";
import { bignumberToBN, BIG_TEN } from "../utils/bg";

export function getCakeVault(): Vault {
	const useTotalDeposit = () => {
		const { account, library } = useWeb3React();
		return useCallback(async () => {
			if (checkInvalid(account, library)) {
				return null;
			}
			const provider = new ethers.providers.Web3Provider(library);
			const cake = new Token(
				"0x0e09fabb73bd3ade0a17ecc321fd13a19e81ce82",
				provider
			);
			return cake.getBalanceNumber(
				"0x73feaa1eE314F8c655E354234017bE2193C9E24E"
			);
		}, [account, library]);
	};

	const useApr = () => {
		const { account, library } = useWeb3React();
		const totalDepositHook = useTotalDeposit();
		return useCallback(async () => {
			if (checkInvalid(account, library)) {
				return null;
			}
			const provider = new ethers.providers.Web3Provider(library);
			const tokenPerBlock = 10;
			const cakeBusdLpContract = "0x804678fa97d91B974ec2af3c843270886528a9E6";
			const price = await getTokenValueUseUSD(cakeBusdLpContract, provider);
			const totDeposite = (await totalDepositHook()) ?? 0;
			const apr = getPoolApr(
				price.toNumber(),
				price.toNumber(),
				totDeposite,
				tokenPerBlock
			);
			return apr;
		}, [account, library, totalDepositHook]);
	};

	const usePrice = () => {
		const { account, library } = useWeb3React();
		return useCallback(async () => {
			if (checkInvalid(account, library)) {
				return null;
			}
			const provider = new ethers.providers.Web3Provider(library);
			const cakeBusdLpContract = "0x804678fa97d91B974ec2af3c843270886528a9E6";
			return getTokenValueUseUSD(cakeBusdLpContract, provider);
		}, [account, library]);
	};

	// CAKE Single
	let cakeSingleVault = new Vault({
		name: "CAKE",
		address: "0xbae67e30012A78094eEB10921477999c927AeB91",
		decimals: 18,
		depositToken: "0x0e09fabb73bd3ade0a17ecc321fd13a19e81ce82",
		poolName: "CAKE Ace Hive ♻️",
		poolDesc: "Offical Syrup",
		nameId: "CAKE-SINGLE",
		earnToken: "CAKE",
		earnTokenAddress: "0x0e09fabb73bd3ade0a17ecc321fd13a19e81ce82",
		logo: cake_token_img,
		strategyAddress: "0x9b520548c4E804348dC21BC78495875A4b9403Cd",
		usePrice: usePrice,
		useApr: useApr,
	});

	const useEstimateProfit = () => {
		const { account, library } = useWeb3React();
		const balanceOfHook = cakeSingleVault.balanceOf();
		const totalSupplyHook = cakeSingleVault.totalSupply();
		return useCallback(
			async (accountx: string) => {
				if (checkInvalid(account, library)) {
					Promise.reject(new Error("web3 error"));
				}
				const provider = new ethers.providers.Web3Provider(library);
				const cakePoolContract = "0x73feaa1eE314F8c655E354234017bE2193C9E24E";
				const pool = getCakeStakingPool(provider, cakePoolContract);
				const strategyContract = "0x9b520548c4E804348dC21BC78495875A4b9403Cd";
				const profit = await pool.pendingCake(0, strategyContract);
				const strategy = getGuaStrategy(provider, strategyContract);
				const total = await strategy.balanceOf();
				return Promise.all([balanceOfHook(accountx), totalSupplyHook()]).then(
					(vals) => {
						return bignumberToBN(profit)
							.plus(bignumberToBN(total))
							.multipliedBy(vals[0])
							.div(vals[1])
							.div(BIG_TEN.pow(18));
					}
				);
			},
			[account, library, balanceOfHook, totalSupplyHook]
		);
	};

	cakeSingleVault.useEstimateProfit = useEstimateProfit;
	return cakeSingleVault;
}

const checkInvalid = (acc: any, lib: any) => {
	return acc === undefined || lib === undefined;
};
