/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable react-hooks/rules-of-hooks */
import { useWeb3React } from "@web3-react/core";
import { BigNumberish, ethers } from "ethers";
import { BigNumber } from "bignumber.js";
import { useCallback, useMemo, useRef, useState } from "react";
import type { Erc20, GuaStrategy, GuaVault } from "../contracts";
import { getERC20, getGuaStrategy, getGuaVault } from "../utils/web3";
import { bignumberToBN, BIG_TEN } from "../utils/bg";
import useRefCallback from "../hooks/useRefCallback";
import useHandleTransactionReceipt from "../hooks/useHandleTransactionReceipt";

import {
	ApprovalState,
	APPROVE_AMOUNT,
	APPROVE_BASE_AMOUNT,
} from "../hooks/useApprove";
import useAllowance from "../hooks/useAllowance";

export enum WriteStatus {
	UNKNOWN,
	PENDING,
	SUCCESS,
}

interface IVault {
	name: string;
	address: string;
	decimals: number;
	depositToken: string;
	poolName: string;
	poolDesc: string;
	nameId: string;
	earnToken: string;
	earnTokenAddress: string;
	logo: string;
	strategyAddress: string;

	usePrice: () => () => Promise<null | BigNumber>;
	useApr: () => () => Promise<null | number>;
	useEstimateProfit?: () => (account: string) => Promise<BigNumber>;
}

// Gua Pool - vault 模型
export class Vault {
	name: string;
	address: string;
	decimals: number;
	depositToken: string;
	poolName: string;
	poolDesc: string;
	nameId: string;
	earnToken: string;
	earnTokenAddress: string;
	logo: string;
	strategyAddress: string;

	usePrice: () => () => Promise<null | BigNumber>;
	useApr: () => () => Promise<null | number>;
	useEstimateProfit?: () => (account: string) => Promise<BigNumber>;

	public get guaVault(): GuaVault | null {
		const { account, library } = useWeb3React();
		if (checkInvalid(account, library)) {
			return null;
		}
		const provider = new ethers.providers.Web3Provider(library);
		return getGuaVault(provider, this.address);
	}

	public get strategy(): GuaStrategy | null {
		const { account, library } = useWeb3React();
		if (checkInvalid(account, library)) {
			return null;
		}
		const provider = new ethers.providers.Web3Provider(library);
		return getGuaStrategy(provider, this.strategyAddress);
	}

	public get depositTokenContract(): Erc20 | null {
		const { account, library } = useWeb3React();
		if (checkInvalid(account, library)) {
			return null;
		}
		const provider = new ethers.providers.Web3Provider(library);
		return getERC20(provider, this.depositToken);
	}

	constructor(props: IVault) {
		this.name = props.name;
		this.address = props.address;
		this.decimals = props.decimals;
		this.depositToken = props.depositToken;
		this.poolName = props.poolName;
		this.poolDesc = props.poolDesc;
		this.nameId = props.nameId;
		this.earnToken = props.earnToken;
		this.earnTokenAddress = props.earnTokenAddress;
		this.logo = props.logo;
		this.strategyAddress = props.strategyAddress;
		this.usePrice = props.usePrice;
		this.useApr = props.useApr;
	}

	public balanceOf(): (accountAddress: string) => Promise<BigNumber> {
		let vault = this.guaVault;
		return useRefCallback(async (address) => {
			if (vault !== null) {
				return vault.balanceOf(address).then((balance) => {
					return bignumberToBN(balance);
				});
			}
			return Promise.reject(new Error("valut is not defined"));
		});
	}

	public balance(): () => Promise<BigNumber> {
		let vault = this.guaVault;
		return useRefCallback(async () => {
			if (vault !== null) {
				return vault.balance().then((n) => {
					return bignumberToBN(n);
				});
			}
			return Promise.reject(new Error("valut is not defined"));
		});
	}

	// read - totalSupply
	public totalSupply(): () => Promise<BigNumber> {
		let vault = this.guaVault;
		return useRefCallback(async () => {
			if (vault !== null) {
				return vault.totalSupply().then((n) => {
					return bignumberToBN(n);
				});
			}
			return Promise.reject(new Error("valut is not defined"));
		});
	}

	// read - 用户当前存入 + 收益量
	public depositeOf(): (accountAddress: string) => Promise<BigNumber> {
		const balanceOfHook = this.balanceOf();
		const balanceHook = this.balance();
		const totalSupplyHook = this.totalSupply();
		return useCallback(
			async (accountAddress) => {
				return Promise.all([
					balanceOfHook(accountAddress),
					balanceHook(),
					totalSupplyHook(),
				]).then((vals) => {
					const balanceOf = vals[0];
					const balance = vals[1];
					const totalSupply = vals[2];
					if (totalSupply.isEqualTo(0)) {
						return new BigNumber(0);
					}
					return balance.multipliedBy(balanceOf).div(totalSupply);
				});
			},
			[balanceHook, balanceOfHook, totalSupplyHook]
		);
	}

	// read - pricePerFullShare
	public pricePerFullShare(): () => Promise<BigNumber> {
		const vault = this.guaVault;
		return useRefCallback(async () => {
			if (vault !== null) {
				return vault.getPricePerFullShare().then((n) => bignumberToBN(n));
			}
			return Promise.reject(new Error("vault is not defined"));
		});
	}

	// write - 用户存入
	public deposit(): [WriteStatus, (amount: BigNumberish) => Promise<void>] {
		const [status, setStatus] = useState<WriteStatus>(WriteStatus.UNKNOWN);
		const handle = useHandleTransactionReceipt();
		let vault = this.guaVault;
		const deposit = useCallback(
			async (amount: BigNumberish) => {
				setStatus(WriteStatus.UNKNOWN);
				if (vault !== null) {
					const aboutGas = await vault.estimateGas.deposit(amount);
					return handle(
						vault.deposit(amount, {
							gasLimit: Math.round(aboutGas.toNumber() * 1.3),
						})
					)
						.then(() => {
							setStatus(WriteStatus.SUCCESS);
						})
						.then(() => {
							setStatus(WriteStatus.UNKNOWN);
						});
				}
				setStatus(WriteStatus.UNKNOWN);
				return Promise.reject(new Error("vault is not defined"));
			},
			[handle, vault?.address]
		);
		return [status, deposit];
	}

	// write - 用户取出
	public withdraw(): [WriteStatus, (amount: BigNumberish) => Promise<void>] {
		const [status, setStatus] = useState<WriteStatus>(WriteStatus.UNKNOWN);
		const handle = useHandleTransactionReceipt();
		let vault = this.guaVault;
		const withdraw = useCallback(
			async (amount: BigNumberish) => {
				setStatus(WriteStatus.PENDING);
				if (vault !== null) {
					const aboutGas = await vault.estimateGas.withdraw(amount);
					return handle(
						vault.withdraw(amount, {
							gasLimit: Math.round(aboutGas.toNumber() * 1.3),
						})
					)
						.then(() => {
							setStatus(WriteStatus.SUCCESS);
						})
						.then(() => {
							setStatus(WriteStatus.UNKNOWN);
						});
				} else {
					setStatus(WriteStatus.UNKNOWN);
					return Promise.reject(new Error("vault is not defined"));
				}
			},
			[handle, vault?.address]
		);
		return [status, withdraw];
	}

	// write - 用户取出，传入 token 数量
	public withdrawByToken(): [
		WriteStatus,
		(amount: BigNumber) => Promise<void>
	] {
		const [status, setStatus] = useState<WriteStatus>(WriteStatus.UNKNOWN);
		const handle = useHandleTransactionReceipt();
		const balanceHook = this.balance();
		const totalSupplyHook = this.totalSupply();
		let vault = this.guaVault;
		const withdrawByToken = useCallback(
			async (amount: BigNumber) => {
				if (vault === null) {
					return Promise.reject(new Error("vault is not defined"));
				}
				const gwei = amount.multipliedBy(BIG_TEN.pow(18));
				const [balance, total] = await Promise.all([
					balanceHook(),
					totalSupplyHook(),
				]);
				if (balance.isEqualTo(0)) {
					Promise.reject(new Error("div 0"));
				}
				const amountGuaVault = gwei
					.div(balance)
					.multipliedBy(total)
					.toFixed(0)
					.toString();
				// console.log("token gwei", gwei.toString());
				// console.log("balance", balance.toString());
				// console.log("total", total.toString());
				// console.log("amout gua vault: ", amountGuaVault);
				const aboutGas = await vault!.estimateGas.withdraw(amountGuaVault);
				return handle(
					vault!.withdraw(amountGuaVault, {
						gasLimit: Math.round(aboutGas.toNumber() * 1.3),
					})
				)
					.then(() => setStatus(WriteStatus.SUCCESS))
					.then(() => setStatus(WriteStatus.UNKNOWN));
			},
			[handle, vault?.address, balanceHook, totalSupplyHook]
		);
		return [status, withdrawByToken];
	}

	public getTvl(): () => Promise<BigNumber> {
		const strategy = this.strategy;
		return useRefCallback(async () => {
			if (strategy !== null) {
				const res = await strategy.balanceOf();
				return bignumberToBN(res).div(BIG_TEN.pow(this.decimals));
			}
			return Promise.reject(new Error("vault is not defined"));
		});
	}

	public useVaultApprove(): () => Promise<void> {
		const tokenContract = this.depositTokenContract;
		const vaultAddress = this.address;
		const approvalAction = useCallback(async () => {
			if (tokenContract === null) {
				return Promise.reject(new Error("Contract is not defined"));
			}
			const tx = await tokenContract!.approve(
				vaultAddress,
				ethers.constants.MaxUint256
			);
			const recipt = await tx.wait();
			if (recipt.status) {
				console.log("success");
			} else {
				console.log("false");
			}
		}, []);
		return approvalAction;
	}

	public useCheckVaultApprovalStatus(): () => Promise<boolean> {
		const { account } = useWeb3React();
		const tokenContract = this.depositTokenContract;
		const vaultContract = this.guaVault;
		const action = useCallback(async () => {
			if (tokenContract === null || vaultContract === null) {
				console.log(
					`Contract is not defined ${tokenContract} ${vaultContract}`
				);
				return Promise.reject(
					new Error(`Contract is not defined ${tokenContract} ${vaultContract}`)
				);
			}
			if (!account) {
				return Promise.reject(new Error("account is not defined"));
			}
			const response = await tokenContract.allowance(
				account,
				vaultContract.address
			);
			const currentAllowance = new BigNumber(response.toString());
			return currentAllowance.gt(0);
		}, [account, tokenContract, vaultContract]);

		return action;
	}
}

const checkInvalid = (acc: any, lib: any) => {
	return acc === undefined || lib === undefined;
};
