/* eslint-disable react-hooks/rules-of-hooks */
/* eslint-disable jsx-a11y/alt-text */
import { Box, Button, Input, Link } from "@chakra-ui/react";
import { useWeb3React } from "@web3-react/core";
import { useWallet } from "use-wallet";
import React from "react";
import { RouteComponentProps } from "react-router";
import { useVaultStore } from "../../config/valuts";
import { Vault } from "../../models/vault";
import { formattedAddress } from "../../utils/wallet";
import { BigNumber } from "bignumber.js";
import { BIG_TEN, BIG_ZERO } from "../../utils/bg";
import { ERC20Token } from "../../models/erc20";
import { parseUnits } from "ethers/lib/utils";

import "./style.css";
import { ApprovalState } from "../../hooks/useApprove";

export interface IPoolDetail extends RouteComponentProps {}

export const PoolDetail: React.FunctionComponent<IPoolDetail> = (
	props: IPoolDetail
) => {
	const { account, chainId } = useWeb3React();
	const currentPoolName: string = props.match.params["poolName"];
	const vaultStore = useVaultStore();
	const wallet = useWallet();

	const [refreshFlag, setRefreshFlag] = React.useState<boolean>(true);
	React.useEffect(() => {
		setTimeout(() => {
			setRefreshFlag(!refreshFlag);
		}, 1000 * 5);
	}, [refreshFlag]);

	const [vault] = React.useState<Vault>(vaultStore.getVault(currentPoolName)!);

	const [apr, setApr] = React.useState<number>(0);
	const aprHook = vault.useApr();
	React.useEffect(() => {
		if (account === undefined || chainId === undefined) return;
		if (aprHook) {
			aprHook().then((apr) => {
				if (apr) {
					setApr(apr);
					return;
				}
			});
		}
	}, [refreshFlag, account, aprHook, chainId]);

	const [userDeposit, setUserDeposit] = React.useState<number>(0);
	const depositOfHook = vault.depositeOf();
	React.useEffect(() => {
		if (account === undefined || chainId === undefined) return;
		depositOfHook(wallet.account)
			.then((res) => {
				setUserDeposit(res.dividedBy(10 ** 18).toNumber());
			})
			.catch((error) => {
				console.log("err", error);
			});
	}, [refreshFlag, account, chainId, depositOfHook, wallet.account]);

	const [userBalance, setUserBanalce] = React.useState<BigNumber>(BIG_ZERO);
	const priceHook = vault.usePrice();
	React.useEffect(() => {
		if (account === undefined || chainId === undefined) return;
		Promise.all([depositOfHook(wallet.account), priceHook()]).then((res) => {
			const balance = res[0].dividedBy(BIG_TEN.pow(18));
			const price = res[1];
			setUserBanalce(balance.multipliedBy(price!));
		});
	}, [refreshFlag, account, depositOfHook, chainId, priceHook, wallet.account]);

	const [operationType, setOperationType] = React.useState<
		"Deposit" | "Withdraw"
	>("Deposit");

	const [walletBalance, setWalletBalance] = React.useState<BigNumber>(BIG_ZERO);
	const token = new ERC20Token(vault.depositToken);
	const tokenCountOfHook = token.tokenCountOf();
	React.useEffect(() => {
		if (account === undefined || chainId === undefined) return;
		tokenCountOfHook(wallet.account).then((res) => {
			setWalletBalance(res);
		});
	}, [refreshFlag, account, chainId, tokenCountOfHook, wallet.account]);

	const estimateProfitObj = vault.useEstimateProfit;
	let estimateProfitHook = !!estimateProfitObj ? estimateProfitObj() : null;
	const [estimateProfit, setEstimateProfit] =
		React.useState<BigNumber>(BIG_ZERO);
	React.useEffect(() => {
		if (account === undefined || chainId === undefined || !!!estimateProfitObj)
			return;
		estimateProfitHook!(wallet.account).then((res) => {
			setEstimateProfit(res);
		});
	}, [
		refreshFlag,
		account,
		chainId,
		estimateProfitHook,
		estimateProfitObj,
		wallet.account,
	]);

	// Deposit 部分
	const [depositTokenCount, setDepositTokenCount] = React.useState<string>("0");
	const [isDepositInputInvalid, setIsDepositInputInvalid] =
		React.useState<boolean>(false);
	const [depositStatus, depositHook] = vault.deposit();

	// Withdraw 部分
	React.useEffect(() => {
		if (account === undefined || chainId === undefined) return;
	});
	const [withdrawTokenCount, setWithdrawTokenCount] =
		React.useState<string>("0");
	const [isWithdrawInputInvalid, setIsWithdrawInputInvalid] =
		React.useState<boolean>(false);
	const [withdrawStatus, withdrawHook] = vault.withdrawByToken();

	// approve 授权
	const checkApprovedHook = vault.useCheckVaultApprovalStatus();
	const vaultApproveHook = vault.useVaultApprove();
	const [isApproved, setIsApproved] = React.useState<boolean>(false);
	React.useEffect(() => {
		if (account === undefined || chainId === undefined) return;
		checkApprovedHook().then((r) => {
			console.log("approved: " + r);
			setIsApproved(r);
		});
	});

	React.useEffect(() => {});

	const OperationInputs = () => {
		if (operationType === "Deposit") {
			return (
				<BalanceInput
					balanceTips="Wallet Balance: "
					balanceValue={`${walletBalance.toFixed(5)} ${vault.name}`}
					isInvalid={isDepositInputInvalid}
					inputValue={depositTokenCount}
					inputOnChange={(event) => {
						let cur = event.target.value;
						setDepositTokenCount(cur);
						let curNum = Number(cur);
						if (isNaN(curNum) || curNum > walletBalance.toNumber()) {
							setIsDepositInputInvalid(true);
						} else {
							setIsDepositInputInvalid(false);
						}
					}}
					maxButtonOnClick={() => {
						setDepositTokenCount(walletBalance.toString());
					}}
					token={vault.name}
				/>
			);
		} else {
			return (
				<BalanceInput
					balanceTips="Withdrawable: "
					balanceValue={`${userDeposit.toFixed(5)} ${vault.name}`}
					isInvalid={isWithdrawInputInvalid}
					inputValue={withdrawTokenCount}
					inputOnChange={(event) => {
						let cur = event.target.value;
						setWithdrawTokenCount(cur);
						let curNum = Number(cur);
						if (isNaN(curNum) || curNum > userDeposit) {
							setIsWithdrawInputInvalid(true);
						} else {
							setIsWithdrawInputInvalid(false);
						}
					}}
					maxButtonOnClick={() => {
						setWithdrawTokenCount(userDeposit.toString());
					}}
					token={vault.name}
				/>
			);
		}
	};

	const OperationButtons = () => {
		if (!isApproved) {
			return (
				<OperationButton
					title={`Approve ${vault.earnToken}`}
					isDisabled={false}
					onClick={vaultApproveHook}
					isLoading={false}
				/>
			);
		}
		if (operationType === "Deposit") {
			return (
				<OperationButton
					title="Deposit"
					onClick={() => {
						const willDepositTokenCount = Number(depositTokenCount);
						if (isNaN(willDepositTokenCount)) {
							setIsDepositInputInvalid(true);
							return;
						}
						console.log(willDepositTokenCount);
						const amount = new BigNumber(willDepositTokenCount).multipliedBy(
							BIG_TEN.pow(vault.decimals)
						);
						const tot = parseUnits(
							depositTokenCount,
							vault.decimals
						).toString();
						console.log(tot, "new");
						depositHook(tot).then(() => {});
					}}
					isDisabled={isDepositInputInvalid}
				/>
			);
		} else {
			return (
				<OperationButton
					title="Withdraw"
					isDisabled={isWithdrawInputInvalid}
					onClick={() => {
						const willWithDrawTokenCount = Number(withdrawTokenCount);
						if (isNaN(willWithDrawTokenCount)) {
							setIsWithdrawInputInvalid(true);
							return;
						}
						const amount = new BigNumber(willWithDrawTokenCount.toFixed(5));
						withdrawHook(amount).then(() => {});
					}}
				/>
			);
		}
	};

	return (
		<Box
			sx={{
				maxWidth: "600px",
				width: "100%",
			}}
		>
			<Box className="pooldetail-container">
				{/*  Token Detail */}
				<Box className="pooldetail-header-container">
					<Box
						sx={{
							width: "100%",
							display: "flex",
							justifyContent: "center",
						}}
					>
						<img src={vault.logo} width="56px" />
					</Box>

					<Box className="pooldetail-header-title">{vault.poolName}</Box>
					<Box className="pooldetail-header-subtitle">{vault.poolDesc}</Box>
				</Box>

				{/* Pool Data */}
				<Box
					sx={{
						width: "100%",
					}}
				>
					<DetailData title="Compounding" value="🚀 Automatic" isFirst={true} />
					<DetailData title="APR" value={`${apr.toFixed(2)}%`} />
					<DetailData
						title="Contract"
						value={vault.address}
						isContract={true}
					/>
					{!!estimateProfitObj ? (
						<DetailData
							title="Estimate"
							value={`${estimateProfit.toFixed(5)} ${vault.earnToken}`}
						/>
					) : null}
					<DetailData
						title="Deposit (Compound)"
						value={`${userDeposit.toFixed(5)} ${vault.earnToken}`}
					/>
					<DetailData
						title="Balance"
						value={`≈ ${userBalance.toNumber().toFixed(5)} USD`}
					/>
				</Box>

				{/* Farm Control */}
				<Box
					sx={{
						paddingTop: "16px",
						paddingBottom: "10px",
					}}
				>
					<Box className="pooldetail-fram-switch">
						<Box
							className={`pooldetail-farm-switch-button${
								operationType === "Deposit" ? " switch-button-select" : ""
							}`}
							onClick={() => {
								setOperationType("Deposit");
							}}
						>
							Deposit
						</Box>
						<Box
							className={`pooldetail-farm-switch-button${
								operationType === "Withdraw" ? " switch-button-select" : ""
							}`}
							onClick={() => {
								setOperationType("Withdraw");
							}}
						>
							Withdraw
						</Box>
					</Box>
				</Box>

				{/* Wallet Control */}
				<Box>{OperationInputs()}</Box>

				{/* Operation Buttons Group */}
				<Box
					sx={{
						paddingTop: "10px",
						paddingBottom: "20px",
					}}
				>
					{OperationButtons()}
				</Box>
			</Box>
		</Box>
	);
};

interface IOperationButton {
	title: string;
	onClick: () => void;
	isDisabled: boolean;
	isLoading?: boolean;
}

const OperationButton: React.FunctionComponent<IOperationButton> = (props) => {
	return (
		<Button
			colorScheme="pink"
			variant="solid"
			sx={{
				width: "100%",
				height: "46px",
				color: "white",
			}}
			onClick={props.onClick}
			isDisabled={props.isDisabled}
			isLoading={!!props.isLoading}
		>
			{props.title}
		</Button>
	);
};

const DetailLine = () => {
	return (
		<Box
			sx={{
				borderTop: "1px solid rgb(170, 149, 133)",
				height: "1px",
				width: "100%",
				opacity: 0.1,
			}}
		></Box>
	);
};

interface IDetailData {
	title: string;
	value: string;
	isFirst?: boolean;
	isContract?: boolean;
}

const DetailData: React.FunctionComponent<IDetailData> = (props) => {
	return (
		<>
			{props.isFirst ? <DetailLine /> : null}

			<div className="pooldetail-data-container">
				<div className="pooldetail-data-label">{props.title}</div>
				{props.isContract ? (
					<Link href={`https://bscscan.com/address/${props.value}`} isExternal>
						<div className="pooldetail-data-value" style={{}}>
							{formattedAddress(props.value, 6)}
						</div>
					</Link>
				) : (
					<div className="pooldetail-data-value">{props.value}</div>
				)}
			</div>
			<DetailLine />
		</>
	);
};

interface IBalanceInput {
	balanceTips: string;
	balanceValue: string;
	isInvalid: boolean;
	inputValue: string;
	inputOnChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
	maxButtonOnClick: (
		event: React.MouseEvent<HTMLButtonElement, MouseEvent>
	) => void;
	descTips?: string;
	token: string;
}

const BalanceInput: React.FunctionComponent<IBalanceInput> = (props) => {
	return (
		<>
			<Box className="pooldetail-wallet-balance">
				{props.balanceTips}
				{props.balanceValue}
			</Box>
			<Box
				className={`pooldetail-wallet-input-container${
					props.isInvalid ? " input-invalid" : ""
				}`}
			>
				<Input
					value={props.inputValue}
					inputMode="decimal"
					placeholder="0"
					className="pooldetail-wallet-input"
					variant="unstyled"
					fontSize="1.4em"
					errorBorderColor="red.300"
					sx={{
						paddingLeft: "10px",
						paddingRight: "10px",
					}}
					onChange={props.inputOnChange}
				></Input>
				<span className="pooldetail-wallet-token">{props.token}</span>
				<Button
					className="pooldetail-wallet-button"
					onClick={props.maxButtonOnClick}
				>
					MAX
				</Button>
			</Box>
			<Box
				className="pooldetail-wallet-balance"
				sx={{
					fontWeight: "bold",
					paddingTop: "5px",
					color: "rgb(243, 198, 34)",
					opacity: 0.8,
				}}
			>
				{props.descTips ? props.descTips : "Deposit Fee: 0%"}
			</Box>
		</>
	);
};
