/* eslint-disable @typescript-eslint/no-redeclare */
import { useWeb3React } from "@web3-react/core";
import { ethers } from "ethers";
import { useCallback } from "react";
import { getPoolApr } from "../utils/apr";
import { Token } from "../utils/erc20Helper";
import { getTokenValueUseBNB, getTokenValueUseUSD } from "../utils/value";
import hunny_token_img from "../asserts/img/token/token-hunny.png";
import { Vault } from "../models/vault";
import { getCakeStakingPool, getGuaStrategy } from "../utils/web3";
import { bignumberToBN, BIG_TEN } from "../utils/bg";
import { getHunnyChef, getHunnyVaultC } from "../utils/web3+hunny";

export function getHunnyVault(): Vault {
	const useApr = () => {
		const { account, library } = useWeb3React();
		return useCallback(async () => {
			if (checkInvalid(account, library)) {
				return null;
			}
			const provider = new ethers.providers.Web3Provider(library);
			const tokenPerBlock = 0.5;
			const hunnyBnbLpContract = "0x36118142F8C21a1F3fd806D4A34F56f51F33504F";
			const price = await getTokenValueUseBNB(hunnyBnbLpContract, provider);
			const hunny = new Token(
				"0x565b72163f17849832a692a3c5928cc502f46d69",
				provider
			);
			const totDeposite = await hunny.getBalanceNumber(
				"0x24320c20499535d0D7a8F6adFb08e5E3f5694417"
			);
			const apr = getPoolApr(
				price.toNumber(),
				price.toNumber(),
				totDeposite,
				tokenPerBlock
			);
			return apr;
		}, [account, library]);
	};

	const usePrice = () => {
		const { account, library } = useWeb3React();
		return useCallback(async () => {
			if (checkInvalid(account, library)) {
				return null;
			}
			const provider = new ethers.providers.Web3Provider(library);
			const hunnyBnbLpContract = "0x36118142F8C21a1F3fd806D4A34F56f51F33504F";
			return getTokenValueUseBNB(hunnyBnbLpContract, provider);
		}, [account, library]);
	};

	let hunnySingleVault = new Vault({
		name: "HUNNY",
		address: "0x896b7E198fe3385EB50Ec36820059F6C06b1Ada4",
		decimals: 18,
		depositToken: "0x565b72163f17849832a692a3c5928cc502f46d69",
		poolName: "HUNNY Ace Hive ♻️",
		poolDesc: "Offical Ace Hive",
		nameId: "HUNNY-SINGLE",
		earnToken: "HUNNY",
		earnTokenAddress: "0x565b72163f17849832a692a3c5928cc502f46d69",
		logo: hunny_token_img,
		strategyAddress: "0xb419c963B087a98C82195CE5f10D8fcdd57101a1",
		usePrice: usePrice,
		useApr: useApr,
	});

	const useEstimateProfit = () => {
		const { account, library } = useWeb3React();
		const balanceOfHook = hunnySingleVault.balanceOf();
		const totalSupplyHook = hunnySingleVault.totalSupply();

		return useCallback(
			async (accountx: string) => {
				if (checkInvalid(account, library)) {
					Promise.reject(new Error("web3 error"));
				}
				const provider = new ethers.providers.Web3Provider(library);
				const hunnyVault = "0x24320c20499535d0D7a8F6adFb08e5E3f5694417";
				const hunnyChef = "0x5ac6Ca0473FA5a25278898d8b72c7c90E083b32a";
				const guaStrategy = "0xb419c963B087a98C82195CE5f10D8fcdd57101a1";
				const vault = getHunnyVaultC(provider, hunnyVault);
				const chef = getHunnyChef(provider, hunnyChef);
				const profitTotal = await chef.pendingHunny(hunnyVault, guaStrategy);
				const total = await vault.balanceOf(guaStrategy);
				return Promise.all([balanceOfHook(accountx), totalSupplyHook()]).then(
					(vals) => {
						return bignumberToBN(profitTotal)
							.plus(bignumberToBN(total))
							.multipliedBy(vals[0])
							.div(vals[1])
							.div(BIG_TEN.pow(18));
					}
				);
			},
			[account, balanceOfHook, library, totalSupplyHook]
		);
	};

	hunnySingleVault.useEstimateProfit = useEstimateProfit;
	return hunnySingleVault;
}

const checkInvalid = (acc: any, lib: any) => {
	return acc === undefined || lib === undefined;
};
