/* eslint-disable react-hooks/rules-of-hooks */
import { useWeb3React } from "@web3-react/core";
import { ethers } from "ethers";
import { BigNumber } from "bignumber.js";
import { Erc20 } from "../contracts";
import { getERC20 } from "../utils/web3";
import useRefCallback from "../hooks/useRefCallback";
import { bignumberToBN, BIG_TEN } from "../utils/bg";
import { useCallback } from "react";

export class ERC20Token {
	address: string;

	constructor(address: string) {
		this.address = address;
	}

	public get erc20(): Erc20 | null {
		const { account, library } = useWeb3React();
		if (account === undefined || library === undefined) {
			return null;
		}
		const provider = new ethers.providers.Web3Provider(library);
		return getERC20(provider, this.address);
	}

	public balanceOf(): (account: string) => Promise<BigNumber> {
		let erc20 = this.erc20;
		return useRefCallback(async (account: string) => {
			if (erc20 !== null) {
				return erc20.balanceOf(account).then((bal) => bignumberToBN(bal));
			}
			return Promise.reject(new Error("balance is not defined"));
		});
	}

	public decimals(): () => Promise<number> {
		let erc20 = this.erc20;
		return useRefCallback(async () => {
			if (erc20 !== null) {
				return erc20.decimals().then((dec) => dec);
			}
			return Promise.reject(new Error("decimals invalid"));
		});
	}

	public tokenCountOf(): (account: string) => Promise<BigNumber> {
		const balanceOfHook = this.balanceOf();
		const decimalsHook = this.decimals();
		return useCallback(
			async (account: string) => {
				return Promise.all([balanceOfHook(account), decimalsHook()]).then(
					(res) => {
						const balanceOf = res[0];
						const decimals = res[1];
						return balanceOf.dividedBy(BIG_TEN.pow(decimals));
					}
				);
			},
			[balanceOfHook, decimalsHook]
		);
	}
}
