/* eslint-disable react-hooks/rules-of-hooks */
/* eslint-disable jsx-a11y/alt-text */
import { BoxProps, LinkOverlay } from "@chakra-ui/react";
import { LinkBox } from "@chakra-ui/react";
import * as React from "react";
import { Link as RouterLink } from "react-router-dom";
import { BigNumber } from "bignumber.js";
import { Vault } from "../../models/vault";
import { useWeb3React } from "@web3-react/core";
import { getApy } from "../../utils/apy";
import { useWallet } from "use-wallet";
import { BIG_TEN, BIG_ZERO } from "../../utils/bg";

import "./style.css";

interface IPoolCardCustom {
	vault: Vault;
}

type IPoolCard = BoxProps & IPoolCardCustom;

export const PoolCard: React.FunctionComponent<IPoolCard> = (props) => {
	const { account, chainId } = useWeb3React();
	const vault = props.vault;

	const [apr, setApr] = React.useState<number>(0);
	const [apy, setApy] = React.useState<number>(0);

	const aprHook = vault.useApr();
	const priceHook = vault.usePrice();

	React.useEffect(() => {
		if (account === undefined || chainId === undefined) return;
		if (aprHook) {
			aprHook()
				.then((apr) => {
					if (apr) {
						setApr(apr);
						return apr;
					}
				})
				.then(() => priceHook())
				.then((price) => {
					if (price === null) {
						return;
					}
					const currentApy = getApy(apr, price.toNumber());
					setApy(currentApy);
				});
		}
	}, [account, apr, aprHook, chainId, priceHook, vault]);
	const wallet = useWallet();
	const [, setUserDeposit] = React.useState<number>(0);
	const depositOfHook = vault.depositeOf();
	React.useEffect(() => {
		if (account === undefined || chainId === undefined) return;
		depositOfHook(wallet.account)
			.then((res) => {
				setUserDeposit(res.dividedBy(10 ** 18).toNumber());
			})
			.catch((error) => {
				console.log("err", error);
			});
	}, [account, chainId, depositOfHook, wallet.account]);

	const [userBalance, setUserBanalce] = React.useState<BigNumber>(BIG_ZERO);
	React.useEffect(() => {
		if (account === undefined || chainId === undefined) return;
		Promise.all([depositOfHook(wallet.account), priceHook()]).then((res) => {
			const balance = res[0].dividedBy(BIG_TEN.pow(18));
			const price = res[1];
			setUserBanalce(balance.multipliedBy(price!));
		});
	}, [account, depositOfHook, chainId, priceHook, wallet.account]);

	const tvlHook = vault.getTvl();
	const [tvl, setTvl] = React.useState<BigNumber>(BIG_ZERO);
	React.useEffect(() => {
		if (account === undefined || chainId === undefined) return;
		Promise.all([tvlHook(), priceHook()]).then((res) => {
			const curTvl = res[0].multipliedBy(res[1]!);
			setTvl(curTvl);
		});
	});

	return (
		<LinkBox>
			<LinkOverlay as={RouterLink} to={`pool/${vault.nameId}`}>
				<div className="poolcard-container clickable">
					<div
						style={{
							gridArea: "icon",
							placeSelf: "center start",
							display: "flex",
						}}
					>
						<img src={vault.logo ?? ""} />
					</div>
					<div
						style={{
							gridArea: "label",
							placeSelf: "center start",
							display: "flex",
							alignSelf: "center",
							flexDirection: "column",
							marginLeft: "8px",
						}}
					>
						<span
							style={{
								fontSize: "16px",
								fontWeight: "bolder",
								color: "#ec6998",
							}}
						>
							{vault.name.toUpperCase()}
						</span>
						<span
							style={{
								fontSize: "13px",
								fontWeight: "bolder",
								color: "#f087ad",
							}}
						>
							{vault.poolName}
						</span>
					</div>
					<div
						style={{
							gridArea: "rates",
							alignItems: "center",
							placeSelf: "center",
							display: "flex",
							flexDirection: "column",
						}}
					>
						<div className="poolcard-detail-apy">{apr.toFixed(2)}%</div>
						<div className="poolcard-detail-apy-desc">
							{apy ? `APY ${apy.toFixed(2)}%` : ""}
						</div>
						<div className="poolcard-detail-apy-desc">Auto-Compuounding</div>
					</div>
					<div
						style={{
							gridArea: "return",
							display: "flex",
							justifyContent: "space-between",
							alignItems: "center",
						}}
					>
						<span className="poolcard-detail-label">Earn</span>
						<span className="poolcard-detail-value">
							{vault.earnToken.toUpperCase()}
						</span>
					</div>

					<div
						style={{
							gridArea: "balance",
							display: "flex",
							justifyContent: "space-between",
							alignItems: "center",
						}}
					>
						<span className="poolcard-detail-label">Balance</span>
						<span className="poolcard-detail-value">
							${userBalance.toNumber().toFixed(2)}
						</span>
					</div>

					<div
						style={{
							gridArea: "total",
							display: "flex",
							justifyContent: "space-between",
							alignItems: "center",
						}}
					>
						<span className="poolcard-detail-label">Total Deposit</span>
						<span className="poolcard-detail-value">
							${tvl.toNumber().toFixed(2)}
						</span>
					</div>
				</div>
			</LinkOverlay>
		</LinkBox>
	);
};
